/**
 * The following styles get applied inside the editor only.
 */
.wp-block-navigation.is-selected .wp-block-fibosearch-search-nav,
.wp-block-navigation.has-child-selected .wp-block-fibosearch-search-nav {
  margin-right: 25px;
}

.wp-block-fibosearch-search-nav__device-preview-desktop .dgwt-wcas-show-on-preview-desktop {
  display: block;
}
.wp-block-fibosearch-search-nav__device-preview-desktop .dgwt-wcas-show-on-preview-tablet, .wp-block-fibosearch-search-nav__device-preview-desktop .dgwt-wcas-show-on-preview-mobile {
  display: none;
}
.wp-block-fibosearch-search-nav__device-preview-tablet .dgwt-wcas-show-on-preview-desktop, .wp-block-fibosearch-search-nav__device-preview-mobile .dgwt-wcas-show-on-preview-desktop {
  display: none;
}
.wp-block-fibosearch-search-nav__device-preview-tablet .dgwt-wcas-show-on-preview-tablet, .wp-block-fibosearch-search-nav__device-preview-tablet .dgwt-wcas-show-on-preview-mobile, .wp-block-fibosearch-search-nav__device-preview-mobile .dgwt-wcas-show-on-preview-tablet, .wp-block-fibosearch-search-nav__device-preview-mobile .dgwt-wcas-show-on-preview-mobile {
  display: block;
}